


export function validateUser(userName, userPassword){
     return new Promise((resolve, reject)=>{
        const personUrl = `https://swapi.co/api/people/?search=${userName}`;
        fetch(personUrl).then((personData)=>{
            personData.json().then((data)=>{
                console.log(data.results)
                if(data.results.length){
                    const person = data.results[0];
                    if(userPassword === person.birth_year ){
                        resolve({"status":true})
                    }
                    else{
                        resolve({"status":false})
                    }
                }
            })
        })
     })
}

export function searchPlanets(searchString){
    return new Promise((resolve, reject)=>{
        const searchLink = `https://swapi.co/api/planets/?search=${searchString}`
        fetch(searchLink).then((planetData)=>{
            planetData.json().then((data)=>{
                //console.log(data.results)
                resolve(data.results)
            })
            .catch((err)=>{
                reject(err)
            })
        })
    })

}


